.nav{
    position: fixed;
    width: 100%;
    padding: 20px;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
   
    
}

.nav_logo{
   /* position: fixed; */
   /* left: 20px; */

    width: 80px;
    object-fit: contain;
}


.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}

.nav_black{
    background-color: #111;
    height: 30px;
    transition: 150ms;
}